



import React from 'react';
import { Row, Col, Card, Table, Tabs, Tab, Badge, Modal, Button, Form } from 'react-bootstrap';

import Loader from '../../App/layout/Loader/index'



import DEMO from '../../globals/constant';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { toggleUserActivity, getUser } from '../../api/userManagement/users';
import { getRole, getRoles, assignRole } from '../../api/userManagement/roles';


const User = (props) => {


    const [userData, setUserData] = React.useState(null);
    const [rolesArr, setRolesArr] = React.useState([])
    const [userProfile, setUserProfile] = React.useState(null);
    const [loading, setLoading] = React.useState(true);
    const [toggleActivityData, setToggleActivityData] = React.useState({ showModal: false, user: {} })
    const [changeRoleData, setChangeRoleData] = React.useState({ userId: null, roleId: '', showModal: false })

    React.useEffect(() => {
        const controller = new AbortController();
        getUser({ action: 'get', filter: 'id', user_id: props.match.params.id }, controller.signal)
            .then(res => {
                console.log(res.json);
                if (res.json.status) {
                    setUserData(res.json.users[0])
                    setUserProfile(res.json.users[0].profile || null)
                    setLoading(false)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch(err => {
                console.log(err)
            })

        getRoles({ "action": "get", "filter": "none", "start": 0, "end": 999999 }, controller.signal)
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    await setRolesArr(res.json.roles);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
        return () => controller.abort();
    }, [])



    const _triggerToggleActivityModal = (user) => {
        setToggleActivityData({ showModal: true, user: user });
    }

    const _triggerChangeRoleModal = (userId) => {
        setChangeRoleData({ ...changeRoleData, userId: userId, showModal: !changeRoleData.showModal })
    }

    const _roleChange = (e) => {
        console.log(e.target.value)
        setChangeRoleData({ ...changeRoleData, roleId: e.target.value })
    }

    const _updateRole = async () => {
        const requestJson = {
            'action': 'assign',
            'user_id': changeRoleData.userId,
            'role_id': changeRoleData.roleId
        }
        console.log(JSON.stringify(requestJson))
        assignRole(requestJson)
            .then(res => {
                console.log(res)
                if (res.json.status) {
                    console.log(rolesArr)
                    const selectedRole = rolesArr.find(role => {
                        return role.id === parseInt(changeRoleData.roleId)
                    });
                    console.log(selectedRole)
                    setUserData({ ...userData, role_str: selectedRole.name, role: selectedRole.id })
                    setChangeRoleData({ ...changeRoleData, showModal: false, userId: null, roleId: '' });
                    Jhilikka.fire({ icon: 'success', title: `Role of the user changed successfully!`, background: '#dffff0' })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }

            })
    }

    const _toggleActivity = async () => {
        const user = toggleActivityData.user
        const requestJson = {
            'action': user.is_active ? 'deactivate' : 'activate',
            'user_id': user.id
        }
        console.log(requestJson)
        toggleUserActivity(requestJson)
            .then(res => {
                console.log(res);
                if (res.json.status) {
                    setUserData({ ...userData, is_active: !userData.is_active })
                    setToggleActivityData({ ...setToggleActivityData, showModal: false, user: {} })
                    Jhilikka.fire({ icon: 'success', title: `User ${user.is_active ? 'deactivated' : 'activated'} successfully!`, background: '#dffff0' })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch(err => {
                console.log(err)
            })
    }




    if (loading) return <Loader />
    var UserPhoto = <a href={`https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcTcZsL6PVn0SNiabAKz7js0QknS2ilJam19QQ&usqp=CAU`}>
        <img
            src={`https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcTcZsL6PVn0SNiabAKz7js0QknS2ilJam19QQ&usqp=CAU`}
            className="rounded w-25 mx-auto d-block" alt="..."
        />
    </a>

    if (userProfile.profile_image)
        var UserPhoto = <a href={`${DEMO.SERVER_URL}${userProfile.profile_image}`}>
            <img
                src={`${DEMO.SERVER_URL}${userProfile.profile_image}`}
                className="rounded w-25 mx-auto d-block" alt="..."
            />
        </a>




    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" style={{ lineHeight: '33px' }} >{userData.name}</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => _triggerChangeRoleModal(props.match.params.id)}
                    >
                        Change Role
                    </button>
                    {userData.is_active ?
                        <button
                            className="btn btn-danger btn-sm shadow-2 mb-4"
                            onClick={() => _triggerToggleActivityModal(userData)}
                        >
                            <i className="feather icon-user-x text-c-white f-15 m-r-5" />
                            Deactivate
                        </button> :
                        <button
                            className="btn btn-primary btn-sm shadow-2 mb-4"
                            onClick={() => _triggerToggleActivityModal(userData)}
                        >
                            <i className="feather icon-user-check text-c-white f-15 m-r-5" />
                            Activate
                        </button>
                    }


                </div>
            </Row>
            <Row>
                <Col xl={2}></Col>
                <Col xl={8}>
                    <Card>
                        <Card.Body>
                            <h5 className='mb-4'>User Profile</h5>
                            <div className="row d-flex align-users-center">

                                <Col md={2}></Col>

                                <Col md={8}>

                                    {UserPhoto}
                                    <div className='mb-1 border-bottom'><b>Name</b></div>
                                    <div className='mt-1 mb-3'>
                                        {userData.name} {" "}
                                        {userData.is_active ? (<Badge variant="info">Active</Badge>) : (<Badge variant="secondary">Deactivated</Badge>)}
                                    </div>


                                    <div className='mb-1 border-bottom'><b>Username</b></div>
                                    <div className='mt-1 mb-3'>{userData.username}</div>

                                    <div className='mb-1 border-bottom'><b>Email</b></div>
                                    <div className='mt-1 mb-3'>{userData.email}</div>

                                    <div className='mb-1 border-bottom'><b>Role</b></div>
                                    <div className='mt-1 mb-3'>{userData.role_str}</div>

                                    {userProfile ?
                                        <>
                                            <div className="d-flex justify-content-between" >
                                                <div className="w-50">
                                                    <div className='mb-1 border-bottom pr-4'><b>Phone 1</b></div>
                                                    <div className='mt-1 mb-3'>{userProfile.phone_number}</div>
                                                </div>
                                                <div className="w-50 ml-4">
                                                    <div className='mb-1 border-bottom pr-4'><b>Phone 2</b></div>
                                                    <div className='mt-1 mb-3'>{userProfile.phone_number2}</div>
                                                </div>
                                            </div>

                                            <div className='mb-1 border-bottom'><b>Address</b></div>
                                            <div className='mt-1 mb-3'>{userProfile.address}</div>
                                        </>
                                        : null}

                                    <div className='mb-1 border-bottom'><b>Last Login</b></div>
                                    <div className='mt-1 mb-3'>{userData.last_login}</div>

                                </Col>
                                <Col md={2}></Col>
                            </div>



                        </Card.Body>
                    </Card>
                </Col>
                <Col xl={2}></Col>



                {/*--------------- Activate and Deactivate of user---------------- */}
                <Modal
                    style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
                    show={toggleActivityData.showModal}
                    onHide={() => setToggleActivityData({ ...toggleActivityData, showModal: false, user: {} })}
                >
                    <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                        <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Are you sure?</Modal.Title>
                    </Modal.Header>

                    <Modal.Body
                        style={{ paddingLeft: '30px' }}
                    >
                        User will be {toggleActivityData.user.is_active ? 'deactivated' : 'activated'}
                    </Modal.Body>


                    <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                        <Button variant="secondary" onClick={() => setToggleActivityData({ ...toggleActivityData, showModal: false, user: {} })}>
                            Cancel
                            </Button>
                        {toggleActivityData.user.is_active ?
                            <Button variant="danger" onClick={_toggleActivity}>
                                Deactivate
                            </Button> :
                            <Button variant="primary" onClick={_toggleActivity}>
                                Activate
                            </Button>
                        }
                    </div>
                </Modal>

                {/*--------------- Change Role of user---------------- */}
                <Modal
                    centered={true}
                    style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
                    show={changeRoleData.showModal}
                    onHide={() => setChangeRoleData({ ...changeRoleData, showModal: false, userId: null, roleId: '' })}
                >
                    <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                        <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Choose Role</Modal.Title>
                    </Modal.Header>

                    <Modal.Body
                        style={{ paddingLeft: '30px' }}
                    >
                        <Col md={12}>
                            <Form.Group >
                                <Form.Label>Role*</Form.Label>
                                <Form.Control name="role" value={changeRoleData.roleId} onChange={_roleChange} as="select">

                                    <option value='' disabled >Select User Role</option>
                                    {rolesArr.map((instance, idex) => {
                                        return <option key={idex} value={instance.id}>{instance.name}</option>
                                    })}
                                </Form.Control>
                            </Form.Group>
                        </Col></Modal.Body>


                    <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                        <Button variant="secondary" onClick={() => setChangeRoleData({ ...changeRoleData, showModal: false, userId: null, roleId: '' })}>
                            Cancel
                            </Button>
                        {changeRoleData.roleId ?
                            <Button variant="primary" onClick={_updateRole}>
                                Done
                                </Button> :
                            null}

                    </div>
                </Modal>

            </Row>
        </>
    );
}

export default User;
